import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class ThemeService {
  immersive: boolean = false;
  backgroundColor: string = '#0b0c16';
  opacity: number = 0;
  opacityFullHeight: number = 300;
  withHeader: boolean = true;
  withFooter: boolean = true;

  constructor() {}

  public setBackground(backgroundColor: string) {
    this.backgroundColor = backgroundColor;
  }

  public setOpacity(opacity: number) {
    this.opacity = opacity;
  }

  public setOpacityFullHeight(opacityFullHeight: number) {
    this.opacityFullHeight = opacityFullHeight;
  }

  public setWithHeader(withHeader: boolean) {
    this.withHeader = withHeader;
  }

  public setWithFooter(withFooter: boolean) {
    this.withFooter = withFooter;
  }

  public setImmersive(immersive: boolean) {
    this.immersive = immersive;
  }

  public onScroll(offsetTop: number) {
    this.opacity = Math.min(1.0, offsetTop / this.opacityFullHeight);
  }
}
