import { Injectable } from '@angular/core';
import { RestRequestService } from 'src/app/services/rest-request.service';

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  constructor(private requestProvider: RestRequestService) {}

  private buildGetPromise(
    url: string,
    params?: any,
    headers?: any,
    coverBaseUrl?: string,
    coverPort?: string
  ): Promise<any> {
    if (headers === null || headers === undefined) {
      headers = {};
    }

    return new Promise((resolve, reject) => {
      this.requestProvider
        .get(
          {
            url: url,
            params: params,
          },
          headers,
          coverBaseUrl,
          coverPort
        )
        .subscribe(
          (data) => {
            resolve(data);
          },
          (error) => {
            console.error(error);
            reject(error);
          }
        );
    });
  }

  private buildPostPromise(
    url: string,
    params: any,
    body?: any,
    headers?: any,
    coverBaseUrl?: string,
    coverPort?: string
  ): Promise<any> {
    if (headers === null || headers === undefined) {
      headers = {};
    }

    return new Promise((resolve, reject) => {
      this.requestProvider
        .post(
          {
            url: url,
            params: params,
            body: body,
          },
          headers,
          coverBaseUrl,
          coverPort
        )
        .subscribe(
          (data) => {
            resolve(data);
          },
          (error) => {
            console.error(error);
            reject(error);
          }
        );
    });
  }

  private buildPutPromise(
    url: string,
    params: any,
    body?: any,
    headers?: any,
    coverBaseUrl?: string,
    coverPort?: string
  ): Promise<any> {
    if (headers === null || headers === undefined) {
      headers = {};
    }

    return new Promise((resolve, reject) => {
      this.requestProvider
        .put(
          {
            url: url,
            params: params,
            body: body,
          },
          headers,
          coverBaseUrl,
          coverPort
        )
        .subscribe(
          (data) => {
            resolve(data);
          },
          (error) => {
            console.error(error);
            reject(error);
          }
        );
    });
  }

  private buildTestPromise(response: any) {
    return new Promise<any>((resolve, reject) => {
      setTimeout(() => {
        resolve(response);
      }, 2000);
    });
  }

  // 登录
  login(params = null) {
    return this.buildPostPromise('/user/login', params);
  }

  /**
   * 获取广告列表
   * @param params
   * @returns
   */
  fetchAdvertList(params = null) {
    return this.buildGetPromise('/advert/list', params);
  }

  /**
   * 获取类型列表
   * @param params
   * @returns
   */
  fetchCategoryList(params = null) {
    return this.buildGetPromise('/map/categories', params);
  }

  /**
   * 获取文章列表
   * @param params
   * @returns
   */
  fetchArticleList(params = null) {
    return this.buildGetPromise('/article/list', params);
  }

  /**
   * 获取文章详情
   * @param params 
   * @returns 
   */
  fetchArticleData(params = null) {
    return this.buildGetPromise('/article/detail', params);
  }

  /**
   * 提交文章
   * @param params
   * @returns
   */
  submitArticle(params = null) {
    return this.buildPostPromise('/article/add', params);
  }
}
