import { Inject, Injectable, InjectionToken } from '@angular/core';
import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpHeaders,
} from '@angular/common/http';
import { Observable, TimeoutError } from 'rxjs';
import { takeUntil, tap } from 'rxjs/operators';
import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { RuntimeService } from './runtime.service';
import { ModalController } from '@ionic/angular';
import { CommonDialogComponent } from '../modals/common-dialog/common-dialog.component';
import { ActivationEnd, Router } from '@angular/router';
import { RestConfig } from '../config/rest-config';
import { RequestCancelService } from './request-cancel.service';

@Injectable({
  providedIn: 'root',
})
export class InterceptorService implements HttpInterceptor {
  private modalStatus: boolean = false;

  constructor(
    private runtime: RuntimeService,
    private modalCtrl: ModalController,
    private router: Router,
    private requestCancelService: RequestCancelService
  ) {
    router.events.subscribe((event) => {
      if (event instanceof ActivationEnd) {
        this.requestCancelService.cancelPendingRequests();
      }
    });
  }

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    if (this.runtime.token) {
      request = request.clone({
        headers: new HttpHeaders({ Authorization: this.runtime.token }),
      });
    } else {
      const url: string = request.url;
      if (url.indexOf('account/inviter') != -1) {
        request = request.clone({
          url: url.replace('account/inviter', 'account/inviter/default'),
        });
      }
    }

    return next.handle(request).pipe(
      takeUntil(this.requestCancelService.onCancelPendingRequests()),
      tap(
        (event) => {
          if (event instanceof HttpResponse) {
            if (event.status >= 500) {
              // server internal error
            } else if (event.status == 200) {
              const response = event.body;
              if (response.code == RestConfig.ACCESS_TOKEN_INVALIDATE) {
                // token invalidate
                setTimeout(() => {
                  if (this.modalStatus) {
                    // only allow just one modal showing
                    return;
                  }
                  this.modalCtrl
                    .create({
                      component: CommonDialogComponent,
                      cssClass: 'common-dialog-modal',
                      componentProps: {
                        title: '提示信息',
                        message: '您的账号在其他设备登录或过期，请重新登录',
                        confirmText: '立即登录',
                      },
                    })
                    .then((modal) => {
                      modal.onDidDismiss().then(({ data }) => {
                        this.modalStatus = false;
                        if (data && data.result.code == 200) {
                          this.runtime.clearCache();
                          setTimeout(() => {
                            this.router.navigate(['/login']);
                          }, 300);
                        }
                      });
                      modal.present();
                      this.modalStatus = true;
                    });
                }, 300);
              } else if (response.code == RestConfig.SYSTEM_UPGRADING) {
                setTimeout(() => {
                  this.router.navigate(['/system-upgrading']);
                }, 300);
              }
            }
          }
        },
        (error) => {
          if (error instanceof TimeoutError) {
            console.log('httpclient connection timeout');
          } else if (error instanceof HttpErrorResponse) {
            console.log('network connection error');
          }
        }
      ),
    );
  }
}
