import { Injectable } from '@angular/core';
import { StoreService } from './store.service';

@Injectable({
  providedIn: 'root'
})
export class UserStoreService {
  private static readonly STORAGE_KEY = 'trend-beat:user';

  constructor(private store: StoreService) { }

  load(): Promise<any> {
    return new Promise(async (resolve, reject) => {
      try {
        const value = await this.store.get(UserStoreService.STORAGE_KEY);
        console.log('[UserStoreService] load() complete:', value);
        if (!value) {
          resolve(null);
        } else {
          const user = JSON.parse(value);
          resolve(user);
        }
      } catch (error) {
        console.error('[UserStoreService] load() error:', error);
        reject(error);
      }
    });
  }

  async save(user: any) {
    try {
      const value = await this.store.set(UserStoreService.STORAGE_KEY, JSON.stringify(user));
      console.log('[UserStoreService] save() complete', value);
    } catch (error) {
      console.error('[UserStoreService] save() error:', error);
    }
  }

  clear() {
    this.store
      .remove(UserStoreService.STORAGE_KEY)
      .then(value => {
        console.log('[UserStoreService] clear() complete');
      })
      .catch(error => {
        console.log('[UserStoreService] clear() error:', error);
      });
  }
}
