import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { IndexComponent } from './layout/index/index.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full',
  },
  {
    path: '',
    component: IndexComponent,
    children: [
      {
        path: 'home',
        loadChildren: () =>
          import('./pages/home/home.module').then((m) => m.HomePageModule),
      },
      {
        path: 'article-detail',
        loadChildren: () => import('./pages/article-detail/article-detail.module').then( m => m.ArticleDetailPageModule)
      },
      {
        path: 'search',
        loadChildren: () => import('./pages/search/search.module').then( m => m.SearchPageModule)
      },
      {
        path: 'search-result',
        loadChildren: () => import('./pages/search-result/search-result.module').then( m => m.SearchResultPageModule)
      },
      {
        path: 'submit-article',
        loadChildren: () => import('./pages/submit-article/submit-article.module').then( m => m.SubmitArticlePageModule)
      },
      {
        path: 'about',
        loadChildren: () => import('./pages/about/about.module').then( m => m.AboutPageModule)
      },
    ],
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
