import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { RestConfig } from "../config/rest-config";

@Injectable({
  providedIn: "root",
})
export class RestRequestService {
  public BASE_URL: string = RestConfig.BASE_URL;
  public PORT: string = RestConfig.DEFAULT_PORT;

  constructor(public http: HttpClient) {}

  serialize(params: any) {
    let result = [];
    for (let key in params) {
      if (params.hasOwnProperty(key)) {
        result.push(
          encodeURIComponent(key) + "=" + encodeURIComponent(params[key])
        );
      }
    }
    return result.join("&");
  }

  get(
    request: any,
    headers?: any,
    coverBaseUrl?: string,
    coverPort?: string
  ): Observable<any> {
    const mBaseUrl = coverBaseUrl ? coverBaseUrl : this.BASE_URL;
    const mPort = coverPort ? coverPort : this.PORT;
    let urlParams = "";
    if (request.params) {
      urlParams = "?" + this.serialize(request.params);
    }
    const mHeaders = new HttpHeaders(headers);
    const fullUrl =
      mBaseUrl + (mPort == "none" ? "" : ":" + mPort) + request.url + urlParams;
    return headers
      ? this.http.get(fullUrl, { headers: mHeaders })
      : this.http.get(fullUrl);
  }

  post(
    request: any,
    headers?: any,
    coverBaseUrl?: string,
    coverPort?: string
  ): Observable<any> {
    const mBaseUrl = coverBaseUrl ? coverBaseUrl : this.BASE_URL;
    const mPort = coverPort ? coverPort : this.PORT;
    const mHeaders = new HttpHeaders(headers);
    const fullUrl =
      mBaseUrl + (mPort == "none" ? "" : ":" + mPort) + request.url;
    return headers
      ? this.http.post(fullUrl, request.body, {
          headers: mHeaders,
          params: request.params,
        })
      : this.http.post(fullUrl, request.body, { params: request.params });
  }

  put(request: any,
    headers?: any,
    coverBaseUrl?: string,
    coverPort?: string): Observable<any> {
      const mBaseUrl = coverBaseUrl ? coverBaseUrl : this.BASE_URL;
    const mPort = coverPort ? coverPort : this.PORT;
    const mHeaders = new HttpHeaders(headers);
    const fullUrl =
      mBaseUrl + (mPort == "none" ? "" : ":" + mPort) + request.url;
    return headers
      ? this.http.put(fullUrl, request.body, {
          headers: mHeaders,
          params: request.params,
        })
      : this.http.put(fullUrl, request.body, { params: request.params });
  }
}
