import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class ScreenFitService {
  screenCategory: 'desktop' | 'pad' | 'phone' = 'desktop';
  isFitPad: boolean = false;
  isFitPhone: boolean = true;

  constructor() {}

  public setScreenCategory(screenCategory: 'desktop' | 'pad' | 'phone') {
    this.screenCategory = screenCategory;
  }

  public setFitPad(isFitPad: boolean) {
    this.isFitPad = isFitPad;
  }

  public setFitPhone(isFitPhone: boolean) {
    this.isFitPhone = isFitPhone;
  }
}
