import { Injectable } from '@angular/core';
import { UserStoreService } from './user-store.service';
import { StoreService } from './store.service';

@Injectable({
  providedIn: 'root'
})
export class RuntimeService {
  user: any = null;
  token: string = "";
  userAgent: string = "";

  constructor(private userStore: UserStoreService, private storage: StoreService) { }

  cacheUser(user: any, persistentStorage = true) {
    this.user = user;
    
    if (persistentStorage) {
      this.userStore.save(user);
    }
  }

  clearCache() {
    this.user = null;
    this.userStore.clear();
    this.token = null;
    this.storage.remove("trend-beat-access-token");
  }

  cacheUserAgent() {
    this.userAgent = navigator.userAgent.toUpperCase();
  }

  isWechatBrowser() {
    return this.userAgent && this.userAgent.indexOf("MICROMESSENGER") != -1;
  }

  isAndroidBrowser() {
    return this.userAgent.indexOf('ADR') > -1 || this.userAgent.indexOf('ANDROID') > -1;
  }

  isIosBrowser() {
    return !!this.userAgent.match(/\(I[^;]+;( U;)? CPU.+MAC OS X/);
  }

  cacheToken(token: string) {
    this.token = token;
    this.storage.set("trend-beat-access-token", token);
  }

  async readToken() {
    let token = await this.storage.get("trend-beat-access-token");
    this.token = token;
  }
}
