import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage-angular';
import * as localforage from 'localforage';

@Injectable({
  providedIn: 'root'
})
export class StoreService {
  static readonly DRIVERS = [
    localforage.INDEXEDDB,
    localforage.LOCALSTORAGE
  ];

  private isReady = false;
  private storages: LocalForage[] = [];

  constructor(private ionicStorage: Storage) {
    this.storages.length = 0;
    StoreService.DRIVERS.forEach(async driver => {
      try {
        const storage = localforage.createInstance({
          driver: localforage.LOCALSTORAGE
        });
        this.storages.push(storage);
      } catch (error) {
        console.error(error);
      }
    });
  }

  async loadStoreItem(key: string, defaultValue?: any) {
    let item = defaultValue ? defaultValue : {};
    try {
      const value = await this.get(key);
      const object = JSON.parse(value);
      item = object ? object : {};
      console.log(`[StoreService] '${key}' loaded: ` + JSON.stringify(item));
    } catch (error) {
      console.error(`[StoreService] Loading '${key}' failed: ` + error);
    }
    return item;
  }

  async setStoreItemData(itemKey: string, dataKey: string, dataValue: any) {
    let item = {};
    try {
      item = await this.loadStoreItem(itemKey);
      item[dataKey] = dataValue;
      await this.set(itemKey, JSON.stringify(item));
      console.log(`[StoreService] '${itemKey}' saved: ` + JSON.stringify(item));
    } catch (error) {
      console.error(`[StoreService] Saving '${itemKey}' failed: ` + error);
    }
    return item;
  }

  async ready() {
    if (this.isReady) {
      return this;
    }

    await this.ionicStorage.create();
    for (let i = 0; i < this.storages.length; i++) {
      const storage = this.storages[i];
      try {
        if (storage.driver() !== StoreService.DRIVERS[i]) {
          await storage.setDriver(StoreService.DRIVERS[i]);
        }
      } catch (error) {
        console.error(`[StoreService] set driver for ${StoreService.DRIVERS[i]} failed: ` + error);
      }
    }
    this.isReady = true;
    return this;
  }

  async get(key: string): Promise<any> {
    let exception = null;
    let result = null;
    try {
      await this.ready();
      result = await this.ionicStorage.get(key);
      if (!this.isEmpty(result)) {
        console.log('[StoreService] retrived from Storage: ' + result);
        return result;
      }
    } catch (error) {
      console.error('[StoreService] error retriving from Storage: ' + error);
      exception = error;
    }

    for (let i = 0; i < this.storages.length; i++) {
      const storage = this.storages[i];
      try {
        result = await storage.getItem(key);
        if (!this.isEmpty(result)) {
          console.log(`[StoreService] retrived from ${storage.driver()}: ` + result);
          return result;
        }
      } catch (error) {
        console.error(`[StoreService] error retriving from ${storage.driver()}: ` + error);
        exception = error;
      }
    }

    if (result !== null) {
      return result;
    }
    if (exception !== null) {
      throw exception;
    }
  }

  async set(key: string, value: any): Promise<any> {
    let exception = null;
    let result = null;
    try {
      await this.ready();
      result = await this.ionicStorage.set(key, value);
      console.log(`[StoreService] {${key}:${value}} set to ${this.ionicStorage.driver}`);
    } catch (error) {
      console.error('[StoreService] error setting Storage: ' + error);
      exception = error;
    }

    for (let i = 0; i < this.storages.length; i++) {
      const storage = this.storages[i];
      try {
        result = await storage.setItem(key, value);
        console.log(`[StoreService] {${key}:${value}} set to ${storage.driver()}`);
      } catch (error) {
        console.error(`[StoreService] error setting ${storage.driver()}: ` + error);
        exception = error;
      }
    }

    if (result != null) {
      return result;
    }
    if (exception != null) {
      throw exception;
    }
  }

  async remove(key: string): Promise<any> {
    let exception = null;
    let result = null;
    try {
      await this.ready();
      result = await this.ionicStorage.remove(key);
    } catch (error) {
      console.error('[StoreService] error removing from Storage: ' + error);
      exception = error;
    }

    for (let i = 0; i < this.storages.length; i++) {
      const storage = this.storages[i];
      try {
        result = await storage.removeItem(key);
      } catch (error) {
        console.error(`[StoreService] error removing from ${storage.driver()}: ` + error);
        exception = error;
      }
    }

    if (exception != null) {
      throw exception;
    }
    return result;
  }

  private isEmpty(argument: any): boolean {
    return argument === null || argument === undefined || (argument.hasOwnProperty('length') && argument.length < 1);
  }
}
