import { ScreenFitService } from '../providers/screen-fit.service';

export class ScreenFit {
  private static instance: ScreenFit = null;

  private constructor() {}

  public static getInstance() {
    if (ScreenFit.instance == null) {
      ScreenFit.instance = new ScreenFit();
    }
    return ScreenFit.instance;
  }

  public init(screenFitService: ScreenFitService) {
    const screenWidth: number =
      document.documentElement.clientWidth ||
      document.body.clientWidth ||
      window.screen.width;
    screenFitService.setScreenCategory(
      screenWidth >= 1200 ||
        (!screenFitService.isFitPad && !screenFitService.isFitPhone)
        ? 'desktop'
        : screenWidth >= 768 && screenFitService.isFitPad
        ? 'pad'
        : 'phone'
    );

    window.onresize = () => {
      const screenWidth: number =
        document.documentElement.clientWidth ||
        document.body.clientWidth ||
        window.screen.width;
      screenFitService.setScreenCategory(
        screenWidth >= 1200 ||
          (!screenFitService.isFitPad && !screenFitService.isFitPhone)
          ? 'desktop'
          : screenWidth >= 768 && screenFitService.isFitPad
          ? 'pad'
          : 'phone'
      );
    };
  }
}
