import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { IonicStorageModule } from '@ionic/storage-angular';
import { ServiceWorkerModule } from '@angular/service-worker';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { InterceptorService } from './services/interceptor.service';
import { RestRequestService } from './services/rest-request.service';
import { ApiService } from './services/api.service';
import { StoreService } from './services/store.service';
import { UserStoreService } from './services/user-store.service';
import { RuntimeService } from './services/runtime.service';
import { EventBusService } from './services/event-bus.service';
import { UserStatusGuard } from './guards/user-status.guard';
import { FormsModule } from '@angular/forms';
import { environment } from 'src/environments/environment';
import { ThemeService } from './providers/theme.service';
import { ScreenFitService } from './providers/screen-fit.service';
import { ChooseMediaTypeComponent } from './modals/choose-media-type/choose-media-type.component';
import { SubmitSuccessComponent } from './modals/submit-success/submit-success.component';

@NgModule({
  declarations: [
    AppComponent,
    ChooseMediaTypeComponent,
    SubmitSuccessComponent,
  ],
  entryComponents: [
    ChooseMediaTypeComponent,
    SubmitSuccessComponent,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot({ animated: false }),
    IonicStorageModule.forRoot(),
    AppRoutingModule,
    FormsModule,
    HttpClientModule,
    ServiceWorkerModule.register('ngsw-worker.js', {
      enabled: environment.production,
    })
  ],
  providers: [
    RestRequestService,
    ApiService,
    StoreService,
    UserStoreService,
    RuntimeService,
    EventBusService,
    UserStatusGuard,
    ThemeService,
    StoreService,
    ScreenFitService,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
    { provide: HTTP_INTERCEPTORS, useClass: InterceptorService, multi: true },
  ],
  bootstrap: [AppComponent],
  schemas: [NO_ERRORS_SCHEMA]
})
export class AppModule {}
