import { Component } from '@angular/core';

import { Platform } from "@ionic/angular";
import { ScreenFitService } from './providers/screen-fit.service';
import { RuntimeService } from "./services/runtime.service";
import { UserStoreService } from "./services/user-store.service";
import { IconRegister } from "./utils/icon-register";
import { ScreenFit } from "./utils/screen-fit";

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  constructor(
    private platform: Platform,
    private userStore: UserStoreService,
    private runtime: RuntimeService,
    private screenFitService: ScreenFitService
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // default style
      // this.statusBar.styleDefault();

      if (this.platform.is("capacitor")) {
        // native device do some thing initial here
      }

      // force dark theme
      document.body.classList.toggle('dark', true);

      // register icons
      IconRegister.register();

      // init screen fit
      ScreenFit.getInstance().init(this.screenFitService);

      // read user info caceh
      this.userStore
        .load()
        .then((user) => {
          if (user) {
            this.runtime.cacheUser(user, false);
          }
        })
        .catch(() => {});

      this.runtime.cacheUserAgent();
      this.runtime.readToken();
    });
  }
}
