import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-choose-media-type',
  templateUrl: './choose-media-type.component.html',
  styleUrls: ['./choose-media-type.component.scss'],
})
export class ChooseMediaTypeComponent implements OnInit {
  constructor(
    private router: Router,
    private modalController: ModalController
  ) {}

  ngOnInit() {}

  onClickMediaType(mediaType: 'picture' | 'video') {
    this.router.navigate(['/submit-article'], {
      queryParams: {
        media: mediaType,
      },
    });
    
    this.modalController.dismiss();
  }

  onClickCloseDialog() {
    this.modalController.dismiss();
  }
}
